@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&display=swap);
body {
    /* background: #5f2c82; 
        background: -webkit-linear-gradient(to right, #49a09d, #5f2c82);  
        background: linear-gradient(to right, #49a09d, #5f2c82); 
    */
    color: #fddab6 !important;
    background-color: #ffeedc;                        
}

.my-container { 
    padding: 12vh 25px;
}

.header-logged-in {
    margin-top: 20px;
    color: #4f4f4f;
    text-align: center;
}
.login-button {
    margin-top: 50px !important;
    background-color: #57949a;
    cursor: pointer;
    width: 150px;
    color: #ffeedc !important;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid #ffeedc;
    padding: 5px;
    border-radius: 9px;
    font-size: 25px;
}
.form {
    max-width: 600px;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form input, .form textarea {
    font-size: 18px;
    font-weight: 600;
    border: 2px solid transparent;
    margin: 5px;
    width: 75%;
    box-shadow: none;
    color: black;
}
.form button {
    margin-top: 20px;
}
.form input:focus, .form textarea:focus {
    border: none !important;
    box-shadow: none;
} 
.sign-up-area {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.sign-up-area a {
    text-decoration: none;
    border-bottom: 1px solid #ffeedc;
    color: #ffeedc;
    font-size: 20px;
    font-weight: 600;
}

.user-list {
    list-style: none;
}


.search-input {
    display: flex;
    max-width: 600px;
    width: 80%;
    padding: 10px;
    margin: 0 auto;
    margin-top: 30px;
    background-color: #ffffff;
    border-radius: 35px;
    border: 1px solid #4f4f4f;
    font-size: 16px;
    color: #4f4f4f;
    font-weight: 600;
}
.search-input:active, .search-input:focus {
    color: #4f4f4f;
    border-color: black;
    box-shadow: none;
    background-color: #ffffff;
    opacity: 0.85;
}
.pet-icon {
    width: 40px
}
.modal-dialog {
    margin: 0 auto;
    width: 280px
}
.modal-content{
    border-radius: 15px;
    background-color: #57949a;
}
.modal-body {
    border-radius: 40px;
}
.modal-body h3{
    text-align: center;
    color: #ffeedc;
}
.modal-footer {
    display: flex;
    justify-content: center;
}
.modal-footer button {
    color: #57949a;
    border-color: #ffeedc;
    background-color: #ffeedc;
}
.modal-footer button:hover, .modal-footer button:active, .modal-footer button:focus {
    color: #ffeedc !important;
    border-color: #ffeedc !important;
    background-color: #57949a !important;
}
@media only screen and (max-width: 560px) {
    .my-container { 
        padding: 12vh 25px
    }
}
.my-nav {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    min-height: 7vh;
    font-weight: 600;
    margin: 0 auto;
    position: fixed;
    background-color: #ffeedc;
    z-index: 2;
    top: 0px;
}

.nav-links {
    justify-content: space-around;
    align-items: center;
    display: flex;
    width: 100%;
    justify-self: flex-end;
}
.nav-links ul {
    width: 100%;
    list-style: none;
    display: flex;
    justify-content: space-around;
    margin: 0 !important;
    padding: 0;
}
.nav-links a {
    color: black;
    text-decoration: none;
    letter-spacing: 2px;
    font-size: 14px;
}

.logo {
    margin-left: 25px;
    letter-spacing: 3px;
    color: #57949a;
    font-family: 'Berkshire Swash', cursive;
    font-size: 50px;
    width: 30%;
    /* text-shadow: -2px 0 #4f4f4f, 0 2px #4f4f4f, 2px 0 #4f4f4f, 0 -2px #4f4f4f; */
}


.icon__icon {
    fill: #4f4f4f;
}

@media screen and (max-width:560px) {
    
    body {
        overflow-x: hidden;
    }
    .mycontainer {
        padding: 0px 50px !important;
    }

    .logo {
        background-color: #ffeedc;
        position: fixed;
        top: 0px;
        width: 100%;
    }
    .my-nav{
        border-top: 2px solid #4f4f4f;
        margin-left: 0px;
        top: auto;
        top: initial;
        bottom: 0px;
    }
    .nav-links {
        justify-content: center !important;
        z-index: 0;
    }
    .nav-links ul {
        width: 100%;
        /* height: 80%; */
        display: flex;
        /* flex-direction: column; */
        position: fixed;
        justify-content: space-around;
        align-items: flex-start;
        background: transparent;
        padding-left: 5px !important;
    }
    .nav-links ul li {
        display: flex;
        justify-content: flex-start;
        /* margin: 5px; */
    }
    .nav-links {
        background: transparent;
        position: fixed;
        /* left: 0px; */
        /* height: 100vh; */
        /* top: 0vh; */
        width: 100%;
        display: flex;
        opacity: 0.90;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
         /* transform: translateX(100%); */
        transition: transform 0.3s ease-in;
        z-index: 1;
        border-radius: 15px;
    }

    .nav-active {
        transform: translateX(-30%);
    }
}
.my-pet-page p{
    color :white;
    font-size: 20px
}

.pets-container {
    margin-bottom: 12vh;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(4, 20vw);
    grid-row-gap: 20px;
    row-gap: 20px;
    z-index: 0;
}
.pet-card {
    color: #ffeedc;
    padding: 10px;
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    align-items: center;
    border: 1px solid transparent;
    width: 180px;
    border-radius: 9px;
    background-color: #57949a;
    justify-self: center;
}

.pet-card a {
    margin-top: 7px;
    color: #4d4d4d;
    background-color: #ffeedc;
    padding: 5px;
    border: 1px solid #ffeedc;
    border-radius: 30px;
}

.pet-card a:hover {
    text-decoration: none;
    opacity: 0.8;
}
.pet-pic-container {
    width: 140px;
    height: 140px;
}
.pet-picture {
    width: 100%;
    height: 100%;
    border: 1px solid rgb(107, 137, 165);
    border-radius: 50%;
}

.pets-navbar {
    height: 100%;
    display: flex;
    justify-content: flex-start;
    /* margin-top: 40px !important; */
    background-color: #ffeedc;
    position: fixed;
    left: 8px;
    /* top: 30%; */
    /* transform: translateY(-100%); */
    z-index: 1;
    /* bottom: 20px; */
    /* border-radius: 40px; */
    /* position: fixed; */
}

.pets-navbar ul {
    margin-top: 4px;
    margin-bottom: 0px;
    padding-left: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    list-style: none;
}
.pets-navbar li {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 30px 0px;
} 
.pets-navbar div {
    color: #4f4f4f
}
@media only screen and (max-width: 1175px) {
    .pets-container {
        grid-template-columns: repeat(3, 28vw);
    }
}

@media only screen and (max-width: 875px) {
    .pet-card {
        width: 165px;
    }
    .pets-container {
        grid-template-columns: repeat(3, 30vw);
    }
}

@media only screen and (max-width: 635px) {
    .pet-card {
        width: 180px;
    }
    .pets-container {
        grid-template-columns: repeat(2, 38vw);
    }
}
@media only screen and (max-width: 560px) {
    .pets-navbar {
        top: 75px;
    }
}
@media only screen and (max-width: 510px) {
    .pet-card {
        width: 250px;
    }
    .pets-container {
        grid-template-columns: repeat(1, 40vw);
    }
}


.pet-page-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px;
    color: #ffeedc;
    font-size: 18px;
    margin: 0 auto;
    max-width: 600px;
    border: 2px solid transparent;
    border-radius: 20px;
    background-color: #57949a;
}

.pet-page-picture {
    margin-top: 20px !important;
    margin: 0 auto;
    width: 50%;
    border-radius: 10px;
    border: 1px solid #ffeedc
}

.pet-page-button {
    background-color: #ffeedc;
    color: #605f5d;
    border-radius: 40px;
    padding: 5px 20px;
}

.pet-page-button:hover {
    background-color: #ffeedc;
    color: #605f5d;
    opacity: 0.85;
}

.infos-table{
    width: 100%;
    border: none !important;
    width: 80%;
    text-align: center;
    margin-top: 20px;
}
.infos-table td {
    border: none !important;
}
.name-info {
    color: #262626;
}

.name-value {
    text-decoration: underline;
    color: #ffeedc;
    font-weight: 500;
}
.profile-page-container {
    display:flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    margin-top: 50px !important
}

.profile-page-container form {
    width: 100%;
}

.profile-picture {
    border: 2px solid #000000;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #000000;
    font-weight: 600;
}
.profile-page-container input, .profile-page-container textarea{
    width: 100%;
    background-color: transparent;
    color: #000000;
    font-weight: 600;
    transition: background-color 0.3s ease-out, border-color 0.3s ease-in;
    font-size: 20px;
    border:2px solid #000000;
    resize: none;
}
.profile-page-container input:disabled, .profile-page-container textarea:disabled {
    transition: background-color 0.3s ease-in  border-color 0.3s ease-in;
    background-color: transparent;
    border-color: transparent;
    font-size: 20px;
    color: #000000;
    font-weight: 600;
}

.profile-page-container label {
    margin-left: 5px;
    color:#000000;
    border-bottom:1px solid #000000;
    padding: 3px 7px;
    text-align: center;
}
.profile-page-container div {
    margin: 5px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.profile-page-container form {
    border: 2px solid #000000;
    border-radius: 20px;
    padding: 5px;
}
.profile-page-container input, .profile-page-container textarea{
    margin: 0px;
}
.password-div input{
    margin: 5px 0px;
}
.edit-btn, .save-btn {
    margin:0px auto!important;
    padding: 5px 30px;
    /* right: 20vw; */
    background-color: #000000;
    z-index: 1;
}
.edit-save-div {
    width: 100%;
    position: fixed;
    top: 70px;
    background-color: #ffeedc;
}

@media only screen and (max-width: 560px) {

    .edit-save-div {
        width: 100%;
        position: fixed;
        top: 70px;
        background-color: #ffeedc;
    }
}
